<template>
  <div class="uk-panel uk-margin-large">
    <nav class="uk-navbar-container uk-margin" uk-navbar>
      <div class="uk-navbar-center">
        <div class="uk-navbar-center-left">
          <div></div>
        </div>
        <a class="uk-navbar-item uk-logo" href="#">AllCountry</a>
      </div>
    </nav>
    <div class="uk-container" uk-filter="target: .js-filter">
      <ul class="uk-subnav uk-subnav-pill uk-text-center">
        <li class="uk-active" uk-filter-control>
          <a href="#">All</a>
        </li>
        <li uk-filter-control=".Africa">
          <a href="#">Africa</a>
        </li>
        <li uk-filter-control=".Americas">
          <a href="#">Americas</a>
        </li>
        <li uk-filter-control=".Asia">
          <a href="#">Asia</a>
        </li>
        <li uk-filter-control=".Europe">
          <a href="#">Europe</a>
        </li>
        <li uk-filter-control=".Oceania">
          <a href="#">Oceania</a>
        </li>
        <li>
          <a class="uk-navbar-toggle" href="#" uk-search-icon></a>
          <div
            class="uk-navbar-dropdown"
            uk-drop="mode: click; cls-drop: uk-navbar-dropdown; boundary: !nav"
          >
            <div class="uk-grid-small uk-flex-middle" uk-grid>
              <div class="uk-width-expand">
                <div class="uk-search uk-search-navbar uk-width-1-1">
                  <input
                    v-model="filtro"
                    class="uk-search-input"
                    type="search"
                    placeholder="Search..."
                    autofocus
                  />
                </div>
              </div>
              <div class="uk-width-auto">
                <a v-on:click="close()" class="uk-navbar-dropdown-close" href="#" uk-close></a>
              </div>
            </div>
          </div>
        </li>
      </ul>
      <ul
        class="js-filter uk-child-width-1-2@s uk-child-width-1-4@m uk-grid uk-text-center"
        uk-grid
      >
        <li v-for="pais in paisesComFiltro" :class="pais.region">
          
          <div class="uk-card uk-card-default">
              <div class="uk-card-header">
                  <div class="uk-grid-small uk-flex-middle" uk-grid>
                      <div class="uk-width-auto">
                          <img class="uk-border-circle" width="40" height="40" :src="pais.flag" :alt="pais.name">
                      </div>
                      <div class="uk-width-expand">
                          <h3 class="uk-card-title uk-margin-remove-bottom">{{ pais.name }}</h3>
                          <p class="uk-text-meta uk-margin-remove-top">{{pais.capital}}</p>
                          <div class="badge uk-label">{{ pais.region }}</div>
                      </div>
                  </div>
              </div>

              <div class="uk-card-footer">
                  <a  v-on:click="abrirModal(pais)" uk-icon="icon: info" href="#modal-full" uk-toggle class="uk-button uk-button-text">Details </a>
              </div>
              
          </div>


          <!--<div class="uk-card uk-card-hover uk-card-body">
            <div class="uk-card-badge uk-label">{{ pais.region }}</div>
             <div class="uk-width-auto">
                <img class="uk-border-circle" width="40" height="40" :src="pais.flag" :alt="pais.name">
            </div>
            <h3 class="uk-card-title uk-margin">{{ pais.name }}</h3>
            <div class="uk-card-media">
              <img :src="pais.flag" :alt="pais.name" />
            </div>
            <p v-if=" pais.capital ">
              <strong>Capital - {{ pais.capital }}</strong>
            </p>
            <p v-else>Capital desconhecida/inexistente</p>
            <a uk-icon="icon: info"
              class="uk-button uk-button-default" v-on:click="abrirModal(pais)"
              href="#modal-full"
              uk-toggle
            >Ver detalhes</a>
          </div>-->
        </li>
      </ul>
      <br><br>
      <h2
        class="uk-text-center"
        v-if="paisesComFiltro==''"
      >No results found, try different keywords or remove search filters.</h2>
    </div>
     <!-- Modal -->
    <modal 
      :name="paisSelecionado.name"
      :flag="paisSelecionado.flag"
      :moeda="paisSelecionado.demonym"
      :capital="paisSelecionado.capital"
      :area="paisSelecionado.area"
      :populacao="paisSelecionado.population"
      >
    </modal>
  </div>
</template>

<script>
import Modal from "../components/Modal";
import UIkit from "../../node_modules/uikit/dist/js/uikit";
export default {
  components: {
    modal: Modal
  },
  name: "hello",
  data() {
    return {
      paises: [],
      filtro: "",
      paisSelecionado: []
    };
  },
  computed: {
    paisesComFiltro() {
      if (this.filtro) {
        let exp = new RegExp(this.filtro.trim(), "i");
        return this.paises.filter(pais => exp.test(pais.name));
      } else {
        return this.paises;
      }
    }
  },
  created() {
    this.$http
      .get("https://restcountries.eu/rest/v2/all")
      .then(res => res.json())
      .then(paises => (this.paises = paises), err => console.log(err));
  },
  methods: {
    close: function() {
      this.filtro = "";
    },
    abrirModal(pais) {
      this.paisSelecionado = pais;
    }
  }
};
</script>

